.back{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.back h1{
    text-align: center;
}

.back div{
    margin-top: 2%;
}

@media ( max-width: 480px ){
    .back{
        display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    flex-direction: column;
    }
}