.shoulder{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.shoulder h1{
    text-align: center;
}

.shoulder div{
    margin-top: 2%;
}

@media ( max-width: 480px ){
    .shoulder{
        display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    flex-direction: column;
    }
}